import React, {useEffect, useState} from "react";
import Input from "../../TypedElements/Input/Input";
import TextArea from "../../TypedElements/TextArea/TextArea";
import Header from "../../TypedElements/Header/Header";
import Checkbox from "../../TypedElements/Checkbox/Checkbox";
import * as styles from './WorkExperience.module.css';

const WorkExperience = function ({handleChange, data}) {


    let [inputs, setInputs] = useState([{beginningOfWork: '', endOfWork: '', organization: '', position: '', responsibilitiesAndAchievements:''}, ]);
    useEffect(()=>{
        handleChange('workExperience', inputs);
    },[inputs])

    const handleRemoveClick = (index) => {
        if (inputs.length <= 1)
            return;
        console.log(inputs[index]);
        inputs.splice(index, 1);
        setInputs([...inputs]);
    };

    const handleAddClick = () => {
        inputs.push({beginningOfWork: '', endOfWork: '', organization: '', position: '', responsibilitiesAndAchievements:''});
        console.log(inputs);
        setInputs([...inputs]);
    };

    const handleInputChange = (index, value, name) => {
        let currentName = name.substr(0, name.indexOf('-'))
        console.log(inputs)
        inputs[index][currentName] = value;
        setInputs([...inputs]);
    };

    const [checked, setChecked] = useState(false)
    const clickedCheckbox = (event) => {
        // let checkBoxCheckedStatus = event.target.checked;
        // console.log(checkBoxCheckedStatus)
        setInputStatus(!isInputClickable)
        console.log(isInputClickable)

    }

    const [isInputClickable, setInputStatus] = useState(true)

    const textAreaPlaceholder = "Рекомендуем указывать не более 10 обязанностей и минимум 1-2 достижения, с конкретными результатами, и цифрами"

    return (
        <div className={"work-experience-container"}>
            <Header header={"ОПЫТ РАБОТЫ"}></Header>
            {inputs && inputs.length ? inputs?.map((value, index) => {
                return <div key={index}> <Input label = {"Начало работы:"} name={`beginningOfWork-${index}`} handleChange={(name, value) => handleInputChange(index, value, name)} value={data.beginningOfWork} placeholder = {"гггг"} maxLen={4}></Input>

                    <div className={styles.inputAndSelect}>
                        <Input isInputClickable = {isInputClickable} label = {"Окончание работы:"} name={`endOfWork-${index}`} value={data.endOfWork} handleChange={(name, value) => {handleInputChange(index, value, name)}}  placeholder = {"мм.гггг"} maxLen={7}></Input>
                        <Checkbox description={"настоящее время"} name={`endOfWorkStatus-${index}`} handleChange={(name, value) => {handleInputChange(index, value, name); clickedCheckbox()}} value={data}></Checkbox>
                    </div>

                    <Input label = {"Организация:"} placeholder = {"Название организации"} name={`organization-${index}`}  value={data.organization} handleChange={(name, value) => handleInputChange(index, value, name)} maxLen={64}></Input>
                    <Input label = {"Должность:"} name={`position-${index}`} placeholder = {"Бухгалтер"} value={data["workExperience"].position} handleChange={(name, value) => handleInputChange(index, value, name)} maxLen={64}></Input>
                    <TextArea name={`responsibilitiesAndAchievements-${index}`}  label = {"Обязанности и достижения:"} value={data.responsibilitiesAndAchievements}
                              placeholder = {textAreaPlaceholder} maxLen={1024} handleChange={(name, value) => handleInputChange(index, value, name)}></TextArea>
                    <div className={styles.buttonsContainer}>
                        {inputs.length > 1 ?  <button className={styles.button} onClick={() => handleRemoveClick(index)}><svg width="16px" height="16px" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg">
<rect width="48" height="48" fill="white" fill-opacity="0.01"/>
<path d="M10 24L38 24" stroke="black" stroke-width="4" stroke-linecap="round" stroke-linejoin="round"/>
</svg></button> : ''}
                        {index === inputs.length - 1 ? <button className={styles.button} onClick={handleAddClick}><svg width="18px" height="18px" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg">
<rect width="48" height="48" fill="white" fill-opacity="0.01"/>
<path d="M24.0607 10L24.024 38" stroke="black" stroke-width="4" stroke-linecap="round" stroke-linejoin="round"/>
<path d="M10 24L38 24" stroke="black" stroke-width="4" stroke-linecap="round" stroke-linejoin="round"/>
</svg>
</button> : ''}
                    </div>

                </div>
            }):''}
        </div>
    );
}

export default WorkExperience;