import React, {useEffect, useState} from "react";
import Input from "../../TypedElements/Input/Input";
import Select from "../../TypedElements/Select/Select";
import Header from "../../TypedElements/Header/Header";
import * as styles from "./Education.module.css";
import Checkbox from "../../TypedElements/Checkbox/Checkbox";
import TextArea from "../../TypedElements/TextArea/TextArea";


const Education = function ({handleChange, data}) {

    let [inputs, setInputs] = useState([{educationalInstitution: '', degree: '',  speciality: '', qualification: '', yearOfEndingEducationalInstitution: ''}, ]);
    useEffect(()=>{
        handleChange('education', inputs);
    },[inputs])

    const handleRemoveClick = (index) => {
        if (inputs.length <= 1)
            return;
        console.log(inputs[index]);
        inputs.splice(index, 1);
        setInputs([...inputs]);
    };

    const handleAddClick = () => {
        inputs.push({educationalInstitution: '', degree: '',  speciality: '', qualification: '', yearOfEndingEducationalInstitution: ''});
        console.log(inputs);
        setInputs([...inputs]);
    };

    const handleInputChange = (index, value, name) => {
        let currentName = name.substr(0, name.indexOf('-'))
        console.log(inputs)
        inputs[index][currentName] = value;
        setInputs([...inputs]);
    };



    const selectOptions = ["Доктор наук","Кандидат наук","Высшее","Неоконченное высшее","Среднее специальное","Среднее"]
    return (
        <div className={"education-data-container"}>
            <Header header={"ОБРАЗОВАНИЕ"}></Header>
            {inputs && inputs.length ? inputs?.map((value, index) => {
                return <div key={index}>  <div className={"education-data"}>
                    <Select label={"Уровень:"} options={selectOptions} name={`degree-${index}`} value={data.degree}handleChange={(name, value) => handleInputChange(index, value, name)}></Select>
                    <Input label = {"Учебное заведение:"} placeholder = {"Название учебного заведения"} name={`educationalInstitution-${index}`}
                           maxLen={128} value={data.educationalInstitution} handleChange={(name, value) => handleInputChange(index, value, name)} ></Input>
                    <Input label = {"Специальность:"} placeholder = {"Экономика"} name={`speciality-${index}`} value={data.speciality} maxLen={128} handleChange={(name, value) => handleInputChange(index, value, name)}></Input>
                    <Input label = {"Квалификация:"} placeholder = {"Бакалавр / Магистр / Экономист"} name={`qualification-${index}`} maxLen={64}  value={data.qualification} handleChange={(name, value) => handleInputChange(index, value, name)} ></Input>
                    <Input label = {"Год окончания:"} placeholder = {"гггг"} name={`yearOfEndingEducationalInstitution-${index}`} value={data.yearOfEndingEducationalInstitution} maxLen={4} handleChange={(name, value) => handleInputChange(index, value, name)} ></Input>

                    <div className={styles.buttonsContainer}>    {inputs.length > 1 ?  <button className={styles.button} onClick={() => handleRemoveClick(index)}><svg width="16px" height="16px" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg">
<rect width="48" height="48" fill="white" fill-opacity="0.01"/>
<path d="M10 24L38 24" stroke="black" stroke-width="4" stroke-linecap="round" stroke-linejoin="round"/>
</svg></button> : ''}
                        {index === inputs.length - 1 ? <button className={styles.button} onClick={handleAddClick}><svg width="18px" height="18px" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg">
<rect width="48" height="48" fill="white" fill-opacity="0.01"/>
<path d="M24.0607 10L24.024 38" stroke="black" stroke-width="4" stroke-linecap="round" stroke-linejoin="round"/>
<path d="M10 24L38 24" stroke="black" stroke-width="4" stroke-linecap="round" stroke-linejoin="round"/>
</svg></button> : ''}</div>

                </div>
                </div >

            }):''}
        </div>
    );
}

export default Education;