import React, {useEffect, useState} from "react";
import Input from "../../TypedElements/Input/Input";
import Header from "../../TypedElements/Header/Header";
import * as styles from "./ContactDetails.module.css"

const ContactDetails = function ({handleChange, data}) {
    let [inputs, setInputs] = useState([{phoneNumber: '+375', email: '', socialProfile: ''}, ]);
    useEffect(()=>{
        handleChange('contactDetails', inputs);
    },[inputs])

    const handleRemoveClick = (index) => {
        if (inputs.length <= 1)
            return;
        console.log(inputs[index]);
        inputs.splice(index, 1);
        setInputs([...inputs]);
    };

    const handleAddClick = () => {
        inputs.push({phoneNumber: '+375', email: '', socialProfile: ''});
        console.log(inputs);
        setInputs([...inputs]);
    };

    const handleInputChange = (index, value, name) => {
        let currentName = name.substr(0, name.indexOf('-'))
        console.log(inputs)
        if (value.length===0){
            data["contactDetails"][index].phoneNumber = "+375" + value;
            inputs[index][currentName] = "+375" + value;
        }
        let x = value.replace(/\D/g, '').match(/(\d{0,3})(\d{0,2})(\d{0,3})(\d{0,2})(\d{0,2})/);
        value = !x[2] ? x[1] :'+' + x[1] + (!x[3] ? x[2] : ' (' + x[2] + ') ') + x[3] + (x[4] ? '-' + x[4] : '')+ (x[5] ? '-' + x[5] : '');
        console.log(index)
        if (value.length<=4){
            data["contactDetails"][index].phoneNumber = "+375" ;
            inputs[index][currentName] = "+375" ;
        } else {
            data["contactDetails"][index].phoneNumber = value
            inputs[index][currentName] = value;
        }

        setInputs([...inputs]);
    };


    return (
        <div className={"contact-details-container"}>
            <Header header={"Контакты"}></Header>
            <div className={"contact-details"}>
                {inputs && inputs.length ? inputs?.map((value, index) => {
                    return( <div className={styles.container} key={index}>
                        <div className={styles.phoneAndButtons}>  <Input label = {"Телефон:"}  placeholder = {"+375 (44) 123-45-67"} name={`phoneNumber-${index}`}
                                      value={data["contactDetails"][index].phoneNumber} handleChange={(name, value) => {
                            handleInputChange(index, value, name);
                        }} maxLen={19}></Input>
                            {/* <div className={styles.buttonsContainer}>
                                {inputs.length > 1 ?  <button className={styles.button} onClick={() => handleRemoveClick(index)}>-</button> : ''}
                                {index === inputs.length - 1 ? <button className={styles.button} onClick={handleAddClick}>+</button> : ''}
                    </div> */}
                        </div>

                    </div>)}):''}

                <Input label = {"Email:"} type={"email"} placeholder = {"your_email@gmail.com"}  name={'email'} value={data.email} handleChange={handleChange} maxLen={512}></Input>
                <Input label = {"Соц. профиль:"} placeholder = {"https://vk.com/id0000000"}  name={'socialProfile'} value={data.socialProfile} handleChange={handleChange} maxLen={2048}></Input>
            </div>
        </div>
    );
}

export default ContactDetails;