import React, {useCallback, useContext, useState} from "react";
import * as styles from './Input.module.css';
import {EmptyInputs} from "../../../context/context";

const   Input = function ({label, placeholder, name, value, handleChange, maxLen, isInputClickable}) {
    let {fields, changeFields} = useContext(EmptyInputs)
    const onChange = (event)=>{
        for(let i=0;i<=fields.length;i++){
            if (fields[i] === event.target.name){
                console.log(fields[i])
                setCondition(false)
                let fieldName = event.target.name
                changeFields(fields.filter(item => item !== fieldName))
            } else {
                setCondition(true)
            }
        }
        console.log(fields)
        handleChange(event.target.name, event.target.value)
    }

    const [isFieldFilled , setCondition] = useState(false)

    return (
        <div className={styles.inputContainer}>
            <div className={styles.label}>{label}</div>
        <div className={styles.rightBlock}>
                <input disabled={isInputClickable} name={name} value={value} className={isFieldFilled ? `${styles.input}` : `${styles.inputError}`} placeholder={placeholder} maxLength={maxLen} onChange={onChange}/>
            </div>
        </div>
    );
}

export default Input