import React, {useEffect, useState} from "react";
import * as styles from './Checkbox.module.css';

const CheckboxAndLabel = function ({description, label, handleChange, name, value}) {

    const [checked, setChecked] = useState(0)
    const clickedCheckbox = (event) => {
        // let checkBoxCheckedStatus = event.target.checked;
        // console.log(checkBoxCheckedStatus)
        console.log("1!!!!!!!!!!!!!!!!!!!!!!!!!")
        if (checked === 0){
            setChecked(1)
        } else {
            setChecked(0)
        }
    }

    useEffect(()=>{
        console.log("qwe")
        console.log(checked)
        handleChange(name,checked)
    }, [ handleChange, name, checked])
        return (
            <div className={styles.container}>
                <div className={styles.label}>{label}</div>
                <input  name={name} value={value}  checked={checked} type={"checkbox"} className={styles.checkbox}>
                </input>
                <label onClick={clickedCheckbox}  name={name} value={value}  htmlFor="checkbox">{description}</label>
                {/*<div className={styles.description}>{description}</div>*/}
            </div>
        );

}

export default CheckboxAndLabel;