import React, {useContext, useState} from "react";
import * as styles from './TextArea.module.css';
import {EmptyInputs} from "../../../context/context";
const TextArea = function ({maxLen, label, placeholder, name, value, handleChange}) {
    let {fields, changeFields} = useContext(EmptyInputs)
    const onChange = (event)=>{
        for(let i=0;i<=fields.length;i++){
            if (fields[i] === event.target.name){
                console.log(fields[i])
                setCondition(false)
                let fieldName = event.target.name
                changeFields(fields.filter(item => item !== fieldName))
            } else {
                setCondition(true)
            }
        }
        console.log(fields)
        handleChange(event.target.name, event.target.value)
    }

    const [isFieldFilled , setCondition] = useState(false)
    return (
        <label className={styles.container}>
            <div className={styles.label}>{label}</div>
            <div className={styles.textAreaWrapper}>
                <textarea name={name} value={value} className={isFieldFilled ? `${styles.input}` : `${styles.inputError}`} placeholder={placeholder} maxLength={maxLen} onChange={onChange}/>
                <div className={styles.lengthStatus}>{value?.length || 0}/{maxLen}</div>
            </div>
        </label>
    );
}

export default TextArea