export function validateMainData(data){
    const necessaryFields = ["name","surname", "patronymic", "workPosition", "expectedSalary"]
    if (!checkIfFieldsAreFilled(data, necessaryFields)) {
        return false;
    } else {
        return true;
    }
}

export function validateContactDetails(data) {
    for (let i = 0; i<= Object.keys(data["contactDetails"][0]).length; i++){
    let preparedData = data["contactDetails"][i];
    console.log(preparedData)
    const necessaryFields = ["phoneNumber", "email", "socialProfile"]
        if(!validatePhoneNumber(preparedData["phoneNumber"])){
            return false;
        }
        if (!validateEmail(preparedData["email"])){
            return false
        }


    if (!checkIfFieldsAreFilled(preparedData, necessaryFields)) {
        return false;
    } else {
        return true;
    }
}}

