import React, {useState} from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import {EmptyInputs} from "./context/context";
import {emptyFields} from "./emptyFields";
function Main(){

    const [fields, changeFields] = useState(emptyFields)
    return (
        <React.StrictMode>
            <EmptyInputs.Provider value={{fields, changeFields}}>
                <App></App>
            </EmptyInputs.Provider>
        </React.StrictMode>

    )
}

ReactDOM.render(
  <Main></Main>,
  document.getElementById('root')
);

