import React, {useEffect, useState} from "react";
import Input from "../../TypedElements/Input/Input";
import Select from "../../TypedElements/Select/Select";
import Header from "../../TypedElements/Header/Header";
import * as styles from "./PersonalData.module.css"
import InputAndCheckbox from "../../TypedElements/Input/InputAndCheckbox";

const PersonalData = function ({handleChange, data}) {
    //
    let [inputs, setInputs] = useState([{placeOfResidence: '', dateOfBirth: '', driverLicence: '', sex: ''}, ]);
    useEffect(()=>{
        handleChange('coursesAndTrainings', inputs);
    },[inputs])
    const sexOptions = ["Не указано","Мужской", "Женский"]

    const handleDateChange = (value, data) => {
        let x = value.replace(/\D/g, '').match(/(\d{0,2})(\d{0,2})(\d{0,4})/);
        console.log(data.dateOfBirth)
        value = !x[2] ? x[1] :  x[1] + '.' + x[2] + (x[3] ? '.' + x[3] : '');
        handleChange("dateOfBirth",value)

    };

    const handleInputChange = (index, value, name) => {
        let currentName = name.substr(0, name.indexOf('-'))

        let x = value.replace(/\D/g, '').match(/(\d{0,3})(\d{0,2})(\d{0,3})(\d{0,2})(\d{0,2})/);
        value = !x[2] ? x[1] :  x[1] + '.' + x[2] + (x[3] ? '.' + x[3] : '');

            data["dateOfBirth"][0].phoneNumber = value
            inputs[index][currentName] = value;

        setInputs([...inputs]);
    };

    return (
        <div className={"personal-data-container"}>
            <Header header={"ЛИЧНЫЕ СВЕДЕНИЯ"}></Header>
            <div className={"personal-data"}>
                <Input label = {"Место проживания:"} placeholder = {"г. Минск"} name={'placeOfResidence'}  handleChange={handleChange} value={data.placeOfResidence} maxLen={95}></Input>
                <Input label = {"Дата рождения:"} placeholder = {"дд.мм.гггг"} name={'dateOfBirth'}  handleChange={(value)=>{handleInputChange(value,data)}} value={data.dateOfBirth} maxLen={10}></Input>
                <div  className={styles.inputAndCheckbox}></div>
                <Input label = {"Водительское:"} placeholder = {"Б"} name={'driverLicence'}  handleChange={handleChange} value={data.driverLicence} maxLen={15}></Input>
                <Select label ={"Пол:"} options={sexOptions} name={"sex"} value={data.sex} handleChange={handleChange}></Select>
            </div>
        </div>
    );
}

export default PersonalData;